  background-repeat: no-repeat;
  background-position: 50% 50%;
  -webkit-transition: background-color 0.3s cubic-bezier(0.64, 0.09, 0.08, 1), -webkit-box-shadow 0.3s cubic-bezier(0.64, 0.09, 0.08, 1);
  transition: background-color 0.3s cubic-bezier(0.64, 0.09, 0.08, 1), -webkit-box-shadow 0.3s cubic-bezier(0.64, 0.09, 0.08, 1);
